
@import "../../global.scss";

.intro {
    background-color: white;
    display: flex;



@include mobile{
    flex-direction: column;
    align-items: center;


}

    .left {
        flex: 0.30;
        overflow: hidden;
        justify-content: center;
        align-items: center;
        padding: 10%;
        margin: 0;


        @include mobile {

            flex: 1;
        }
       

        .imgContainer {
            width: 400px;
            height:400px;
            background-color: rgb(211, 230, 211);
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            float: right;
            
            @include mobile{
                align-items: flex-start;
                float: left;
                width: 300px;
                height: 300px;
                align-items: center;
                justify-content: center;
                display: flex;


            }

            img {
                width: 70%;
                height: 70%;

               
            }
        }
    }

    .right {
        flex: 0.5;
        @include mobile {

            flex: 0.8;
        }
        .wrapper {
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;


            @include mobile{
                padding-left: 0;
                align-items: center;

            }


            h1 {
                font-size: 50px;
                margin: 10px 20px;


                @include mobile {

                    font-size: 40px;
                }


            }
            h2 {
                font-size: 35px;
            }
            h3 {
                font-size: 30px;

                @include mobile {

                    font-size: 20px;
                }

                span{
                    font-size: inherit;
                    color: crimson;
                }
                
                .ityped-cursor{
                    animation: blink 1s infinite;
                }

                @keyframes blink {
                    50%{
                        opacity: 1;
                    }
                    100%{
                        opacity: 0;
                    }
                    
                }

            }
        }
        a{
            position: absolute;
            bottom: 10px;
            left: 60%;

            img{
                width: 40px;
                animation: arrowBlink 2s infinite;
             }

             @keyframes arrowBlink {
                 100%{
                     opacity:0;
                 }
                 
             }
        }

    }
}
