@import "../../global.scss";

.travail {
    background-color: rgb(184, 221, 181);
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .arrow{
        height: 50px;
        position: absolute;
        cursor: pointer;

        @include mobile {

            display: none;
         }

        &.left{
            left: 100px;
           
        }

        &.right{
            right: 100px;
            transform: rotateY(180deg);
        }
    }

    .slider {
        height: 350px;
        display: flex;
        position: absolute;
        left: 0;
        transition: all 1s ease;

        @include mobile {
            height: 100vh;
             flex-direction: column;
             justify-content: center;
            
         }
        .container {
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;

            .item {
                width: 700px;
                height: 100%;
                background-color: #fff;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;

                @include mobile {
                   width: 90%;
                   height: 180px;
                   margin: 5px 0;
                 }

                .left {
                    flex: 4;
                    height: 80%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .leftContainer{
                        width: 90%;
                        height: 70%;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;

                        .imgContainer{
                            width: 40px;
                            height: 40px;
                            border-radius: 50%;
                            background-color: chocolate;
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            .img{
                                width: 25px;
                            }
                        }
                        h2{
                            font-size: 20px;
                        }
                        p{
                            font-size: 13px;
                        }
                        span{
                            font-size: 12px;
                            font-weight: 600;
                            text-decoration: underline;
                            cursor: pointer;
                        }


                    }
                }
                .right {
                    height: 100%;
                    background-color: rgb(240, 54, 29);
                    flex: 8;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    overflow: hidden;
                    
                    img{
                        width: 400px;
                       transform: rotate(-10deg);
                    }
                }
            }
        }
    }
}
